import { Query } from "react-apollo";
import DisplayError from "./ErrorMessage";

const Permissions = props => (
  <Query query={ALL_USERS_QUERY}>
    {({ data, loading, error }) => {
      <div>
        <DisplayError error={error} />
        <p>hello.</p>
      </div>;
    }}
  </Query>
);

export default Permissions;
